"use client";
import { useState, useEffect } from "react";

export default function Header() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 10);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <>
      <header className={`fixed top-0 left-0 w-full z-[5000] transition-all duration-300 ${isScrolled ? "bg-white/95 backdrop-blur-md shadow-md py-3" : "bg-white/50 backdrop-blur-sm py-5"}`}>
        <div className="container mx-auto px-6 flex justify-between items-center">
          <div className="flex-shrink-0">
            <a href="/" className="block">
              <img src="/assets/img/logo.png" alt="MT CONSULTING" className={`transition-all duration-300 object-contain ${isScrolled ? "h-10" : "h-14"}`} />
            </a>
          </div>
          <nav className="hidden lg:flex items-center gap-8">
            {["Inicio", "Nosotros", "Servicios", "Contacto"].map((item) => (
              <a key={item} href={item === "Inicio" ? "/" : `#${item.toLowerCase()}`} className="text-sm font-bold uppercase tracking-widest text-slate-800 hover:text-red-600 transition-colors relative group">
                {item}
                <span className="absolute -bottom-2 left-0 w-0 h-0.5 bg-red-600 transition-all duration-300 group-hover:w-full"></span>
              </a>
            ))}
          </nav>
          <div className="hidden lg:block">
            <a href="https://wa.me/14078029204" target="_blank" className="px-6 py-2 border-2 border-red-600 text-red-600 rounded-full font-bold text-xs uppercase tracking-widest hover:bg-red-600 hover:text-white transition-all duration-300">
              Consulta Gratis
            </a>
          </div>
          <button onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)} className="lg:hidden text-slate-800 text-2xl focus:outline-none">
            <i className={`fa ${isMobileMenuOpen ? "fa-times" : "fa-bars"}`}></i>
          </button>
        </div>
      </header>
      <div className={`fixed inset-0 bg-white z-[4999] flex flex-col justify-center items-center transition-transform duration-300 ${isMobileMenuOpen ? "translate-x-0" : "translate-x-full"}`}>
        <div className="flex flex-col gap-8 text-center">
          {["Inicio", "Nosotros", "Servicios", "Contacto"].map((item) => (
            <a key={item} href={item === "Inicio" ? "/" : `#${item.toLowerCase()}`} onClick={() => setIsMobileMenuOpen(false)} className="text-2xl font-bold uppercase text-slate-900">
              {item}
            </a>
          ))}
        </div>
      </div>
    </>
  );
}